<template>
  <div>
    <div
      class="ListItem"
      v-for="(item, index) in contentList"
      :key="index"
      @click="
        $router.push({
          path: '/shopdetails',
          query: {
            comment_id: item.comment_id,
          },
        })
      "
    >
      <div class="userInfo">
        <img :src="item.user_avatar" alt="" />
        <div class="username">{{ item.user_name }}</div>
      </div>
      <div class="content">{{ item.comment_content }}</div>
      <div class="showImg">
        <img
          v-for="(img, index) in item.comment_images"
          :key="index"
          :src="img"
          alt=""
        />
      </div>
      <!-- 图标 -->
      <div class="btnIcon">
        <div><van-icon name="share-o" size="20" /></div>
        <div style="display: flex; align-items: center">
          <van-icon name="chat-o" size="20" />
          <span>{{ item.comment_num }}</span>
        </div>
        <div style="display: flex; align-items: center">
          <van-icon name="good-job-o" size="20" /><span>{{
            item.praise_num
          }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      contentList: null,
    };
  },
  methods: {
    // 买家秀
    getContentList() {
      this.axios
        .get("communicate/mizone_buyer_show_list", {
          params: {
            product_id: this.$route.query.product_id,
            size: 10,
            tag_id: 58527,
            tag_type: 2,
            page: 1,
          },
        })
        .then((res) => {
          // console.log("getContentList", res.data.data.comments);
          this.contentList = res.data.data.comments;
        });
    },
  },
  created() {
    this.getContentList();
  },
};
</script>

<style lang="less" scoped>
.ListItem {
  padding-top: 0.3rem;
  margin-bottom: 0.3rem;
  &::after {
    margin-left: -0.24rem;
    margin-right: -0.24rem;
    display: block;
    content: "";
    border-bottom: 0.12rem solid #f6f6f6;
  }
  .userInfo {
    display: flex;
    img {
      margin-right: 0.16rem;
      width: 0.52rem;
      height: 0.52rem;
      border-radius: 0.52rem;
      overflow: hidden;
    }
    .username {
      font-size: 0.24rem;
      font-weight: 500;
      color: #161616;
      line-height: 1.4;
    }
  }
  .content {
    font-size: 0.3rem;
    font-weight: 500;
    color: #161616;
    line-height: 1.6;
    word-wrap: break-word;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
  }
  .showImg {
    margin: 0.2rem 0;
    display: flex;
    flex-wrap: wrap;
    border-radius: 10px;
    overflow: hidden;

    img {
      width: 30%;
      height: 2.21rem;
      overflow: hidden;
      object-fit: cover;
      padding: 0.1rem;
    }
  }
  .btnIcon {
    width: 100%;
    display: flex;
    justify-content: space-around;
    padding: 8px 0;
    span {
      font-size: 0.24rem;
      font-weight: 500;
      color: #757575;
      margin-left: 4px;
    }
  }
}
</style>