<template>
  <div>
    <van-search
      v-model="value"
      placeholder="试试搜索你的问题"
      @input="getAnswer"
    />
    <template v-if="value === ''">
      <div class="ListItem" v-for="(item, index) in questionList" :key="index" @click="$router.push({
        path:'/problemdetail',
        query:{
          product_id:$route.query.product_id,
          q_id:item.q_id
        }
      })">
        <div class="question">
          <p>{{ item.question }}</p>
        </div>
        <div class="userInfo">
          <img :src="item?.icon" alt="" />
          <div class="username">
            {{ item?.nickname }} {{ item.create_time }} {{ item.belong_place }}
          </div>
        </div>
        <div
          class="answer"
          v-for="(answer, index) in item.answer_list_v2"
          :key="index"
        >
          <img :src="answer?.icon" alt="" /> <span>{{ answer?.answer }}</span>
        </div>
        <p class="answerNum">全部{{ item.answer_num }}条评论</p>
      </div>
    </template>
    <!-- 搜索结果 -->
    <template v-if="value">
      <p>已找到“{{ value }}”相关的问题{{ answerList?.length }}条</p>
      <div class="ListItem" v-for="(item, index) in answerList" :key="index">
        <div class="question" v-html="item.h_question"></div>
        <p class="answerNum">{{ item.answer_num }}个回答 {{item.follow_num}}个关注</p>
      </div>
    </template>
  </div>
</template>

<script>
export default {
  data() {
    return {
      value: "",
      questionList: null,
      answerList: null,
    };
  },
  methods: {
    // 推荐
    getQuestionList() {
      this.axios
        .get("communicate/question_list", {
          params: {
            product_id: this.$route.query.product_id,
            after: "",
            size: 10,
          },
        })
        .then((res) => {
          // console.log("getRecList", res.data.data);
          this.questionList = res.data.data?.question_list;
          console.log("questionList", this.questionList);
        });
    },
    // 问题搜索
    getAnswer() {
      this.axios
        .get(
          "https://apis.netstart.cn/xmsc/communicate/question_search?product_id=" +
            this.$route.query.product_id +
            "&query=" +
            this.value
        )
        .then((res) => {
          console.log(res.data.data?.question_list);
          this.answerList = res.data.data?.question_list;
        });
    },
  },
  created() {
    this.getQuestionList();
  },
};
</script>

<style lang="less" scoped>
.ListItem {
  padding-top: 0.3rem;
  margin-bottom: 0.3rem;
  &::after {
    margin-left: -0.24rem;
    margin-right: -0.24rem;
    display: block;
    content: "";
    border-bottom: 0.12rem solid #f6f6f6;
  }
  .question {
    line-height: 1.5;
    font-size: 0.28rem;
    font-weight: 700;
    color: #000;
  }
  .userInfo {
    display: flex;
    align-items: center;
    img {
      width: 0.32rem;
      height: 0.32rem;
      border-radius: 0.16rem;
      margin-right: 0.16rem;
    }
    .username {
      margin-top: 0.16rem;
      font-size: 0.24rem;
      color: rgba(0, 0, 0, 0.4);
      padding-bottom: 0.24rem;
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
    }
  }
  .answer {
    font-size: 0.24rem;
    font-weight: 400;
    color: rgba(0, 0, 0, 0.87);
    line-height: 0.28rem;
    position: relative;
    padding-top: 0.12rem;
    display: flex;
    align-items: center;

    img {
      width: 0.32rem;
      height: 0.32rem;
      border-radius: 0.16rem;
      margin-right: 0.16rem;
    }
  }
  .answerNum {
    margin-top: 0.24rem;
    font-size: 0.24rem;
    color: rgba(0, 0, 0, 0.4);
  }
}
</style>