<template>
  <div class="productMi">
    <van-sticky>
      <div class="topTab">
        <van-icon
          name="arrow-left"
          color="#fff"
          size="20"
          @click="$router.back()"
        />
        <div class="title">米圈</div>
        <van-icon name="ellipsis" color="#fff" size="20" />
      </div>
    </van-sticky>
    <div class="top">
      <img :src="communicate?.img_url" alt="" />
      <div>
        <div class="name">{{ communicate?.product_name }}</div>
        <div class="heat">
          {{ communicate?.heat }}热度 | {{ communicate?.join_num }}人参与评论
        </div>
      </div>
      <div class="like">+ 关注</div>
    </div>
    <div class="content">
      <van-tabs v-model="active" animated>
        <van-tab v-for="(tab, index) in tab" :key="index" :title="tab">
        </van-tab>
      </van-tabs>
      <div v-if="active === 0">
        <BuyerRecList />
      </div>
      <div v-if="active === 1">
        <CallFamily />
      </div>
      <div v-if="active === 2">
        <ContentList />
      </div>
      <div v-if="active === 3">
        <BuyerShowList />
      </div>
    </div>
  </div>
</template>
  
  <script>
import ContentList from "@/components/Mi/ContentList.vue";
import BuyerRecList from "@/components/Mi/BuyerRecList.vue";
import BuyerShowList from "@/components/Mi/BuyerShowList.vue";
import CallFamily from "@/components/Mi/CallFamily.vue";

export default {
  components: {
    BuyerShowList,
    BuyerRecList,
    CallFamily,
    ContentList,
  },
  data() {
    return {
      active: 0,
      communicate: null,
      contentList: null,
      tab: ["推荐", "问大家", "评测", "买家秀"],
    };
  },
  watch: {
    active(n) {
      console.log(n);
    },
  },
  methods: {
    // 米圈详情
    getCommunicate() {
      this.axios
        .get(
          "communicate/mizone_home?product_id=" + this.$route.query.product_id
        )
        .then((res) => {
          // console.log("communicate", res.data.data);
          this.communicate = res.data.data;
        });
    },
  },
  activated() {
    this.getCommunicate();
  },
};
</script>
  
  <style lang="less" scoped>
.productMi {
  .topTab {
    color: #fff;
    padding: 0 0.32rem;
    height: 0.88rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
    .title {
      width: 70vw;
      text-align: center;
    }
  }
  .top {
    min-height: 4rem;
    background: url(@/assets/bg.813761e1fe.jpeg) top no-repeat;
    background-size: cover;
    padding-bottom: 0.48rem;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    color: #fff;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    z-index: -1;
    img {
      width: 1.37rem;
      height: 1.37rem;
      background: #fff;
      border-radius: 0.08rem;
      margin: 0 0.32rem;
    }
    .name {
      font-size: 0.36rem;
      font-weight: 500;
      color: #fff;
      line-height: 0.36rem;
    }
    .heat {
      font-size: 0.24rem;
      font-weight: 500;
      color: #fff;
      line-height: 1.4;
    }
  }
  .like {
    width: 1.34rem;
    height: 0.53rem;
    line-height: 0.53rem;
    font-size: 0.24rem;
    font-family: MILanPro--GB1-4, MILanPro--GB1;
    font-weight: 400;
    color: #0e1827;
    background-color: #fff;
    text-align: center;
    border-radius: 1em;
  }
  .content {
    background: #fff;
    border-radius: 0.24rem 0.24rem 0 0;
    padding: 0 0.24rem 0.24rem;
    margin-top: 3.8rem;
    position: relative;
    .van-tab {
      font-size: 0.32rem;
      font-weight: 500;
      color: #333;
      line-height: 0.43rem;
    }
  }
}
</style>
  