<template>
  <div>
    <div
      class="ListItem"
      v-for="(item, index) in contentList"
      :key="index"
      @click="
        $router.push({
          path: '/videodetails',
          query: {
            id: item.id,
          },
        })
      "
    >
      <div class="userInfo">
        <img :src="item?.icon" alt="" />
        <div class="username">{{ item?.nickname }}</div>
      </div>
      <div class="content">{{ item?.content }}</div>
      <div class="showImg">
        <img
          v-for="(img, index) in item?.pic_list"
          :key="index"
          :src="img.img"
          alt=""
        />
        <video
          v-for="(video, index) in item?.video_list"
          :key="index"
          poster="video.cover"
          controls
          muted
        >
          <source :src="video.video_url" type="video/mp4" />
        </video>
      </div>
      <div class="hot">
        <span>{{ item.hot_comment.name }}: </span>
        {{ item.hot_comment.comment }}
      </div>
      <!-- 图标 -->
      <div class="btnIcon">
        <div><van-icon name="share-o" size="20" /></div>
        <div style="display: flex; align-items: center">
          <van-icon name="chat-o" size="20" />
          <span>{{ item.comment_num }}</span>
        </div>
        <div style="display: flex; align-items: center">
          <van-icon name="good-job-o" size="20" /><span>{{
            item.praise_num
          }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      contentList: null,
    };
  },
  methods: {
    // 推荐
    getContentList() {
      this.axios
        .get("communicate/mizone_content_list", {
          params: {
            product_id: this.$route.query.product_id,
            size: 10,
            tag_id: 58527,
            tag_type: 2,
            page: 1,
          },
        })
        .then((res) => {
          console.log("getRecList", res.data.data);
          this.contentList = res.data.data.content_list
          // console.log("contentList", this.contentList);
        });
    },
  },
  created() {
    this.getContentList();
  },
};
</script>

<style lang="less" scoped>
.ListItem {
  padding-top: 0.3rem;
  margin-bottom: 0.3rem;
  &::after {
    margin-left: -0.24rem;
    margin-right: -0.24rem;
    display: block;
    content: "";
    border-bottom: 0.12rem solid #f6f6f6;
  }
  .userInfo {
    display: flex;
    margin-bottom: 0.2rem;
    img {
      margin-right: 0.16rem;
      width: 0.52rem;
      height: 0.52rem;
      border-radius: 0.52rem;
      overflow: hidden;
    }
    .username {
      font-size: 0.24rem;
      font-weight: 500;
      color: #161616;
      line-height: 1.4;
    }
  }
  .content {
    margin-top: 0.16rem;
    font-size: 0.26rem;
    color: #666;
    line-height: 0.43rem;
    word-wrap: break-word;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
  }
  .showImg {
    margin: 0.2rem 0;
    display: flex;
    flex-wrap: wrap;
    img {
      width: 49.6%;
      height: 3.33rem;
      object-fit: cover;
      flex: 1;
    }
  }
  .hot {
    padding: 0.16rem;
    margin-bottom: 0.14rem;
    background: #f6f6f6;
    border-radius: 0.1rem;
    font-size: 0.26rem;
    font-weight: 400;
    color: #161616;
    line-height: 1.34;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    span {
      color: #757575;
    }
  }
  .btnIcon {
    width: 100%;
    display: flex;
    justify-content: space-around;
    padding: 8px 0;
    span {
      font-size: 0.24rem;
      font-weight: 500;
      color: #757575;
      margin-left: 4px;
    }
  }
}
</style>